@font-face {
  font-family: "Nimbus";
  src: url("/fonts/nimbussanl-reg-webfont.woff2") format("woff2"), url("/fonts/nimbussanl-reg-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

* {
  margin: 0;
  padding: 0; }

html {
  font-size: 62.5%; }

body {
  font-family: "Nimbus";
  font-size: 20px; }

.container {
  display: block;
  margin: 0 auto;
  max-width: 1200px;
  width: 100%; }

h1 {
  font-size: 40px;
  font-weight: 300;
  padding-bottom: 30px; }

h2 {
  font-size: 40px;
  padding-bottom: 10px;
  font-weight: 100; }

ul {
  margin: 0; }

li {
  font-size: 20px; }

a {
  color: black;
  outline: none; }

p {
  font-size: 20px;
  line-height: 36px;
  margin: 0; }

button {
  color: #ffffff;
  background-color: #192126;
  margin-top: 30px;
  padding: 15px 30px;
  outline: none;
  border: none; }

#navigation {
  background-color: #ffffff;
  -webkit-box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.15);
          box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.15);
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1; }
  #navigation img {
    float: left;
    width: 283px;
    padding-left: 30px;
    padding-top: 17px; }
    @media screen and (max-width: 768px) {
      #navigation img {
        padding-bottom: 15px; } }
  #navigation ul {
    float: right; }
  #navigation li {
    background-color: #192126;
    color: #ffffff;
    list-style: none;
    padding: 20px 30px;
    pointer: cursor; }
    @media screen and (max-width: 768px) {
      #navigation li {
        display: none; } }

#footer {
  background-color: #192126;
  color: #ffffff;
  padding: 20px;
  text-align: center; }

#hero {
  height: 400px;
  padding: 30px;
  background-image: url("/hero.jpg");
  position: relative;
  z-index: 0;
  margin-top: 60px; }
  #hero img {
    width: 100%;
    max-width: 1000px;
    position: absolute;
    padding: 0 30px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%); }

#about {
  padding: 60px 30px; }

#products {
  background-color: #fafbfc;
  padding: 60px 30px; }
  #products img {
    width: 100%;
    max-width: 500px; }
    @media screen and (max-width: 768px) {
      #products img {
        padding-bottom: 30px; } }
  #products .product {
    padding-bottom: 60px; }
    #products .product:last-of-type {
      padding-bottom: 0; }

#faq {
  padding: 60px 30px; }
  #faq .question-area {
    padding-bottom: 30px; }
  #faq .question {
    font-weight: bold;
    padding-bottom: 15px; }

#distributors {
  background-color: #fafbfc;
  padding: 60px 30px; }
  @media screen and (max-width: 768px) {
    #distributors img {
      padding-bottom: 30px; } }

#contact {
  background-image: url("/contact_bg.jpg");
  color: #ffffff;
  padding: 60px 30px; }
  #contact input,
  #contact textarea {
    border: none;
    padding: 15px;
    width: 100%;
    outline: none;
    margin-bottom: 30px; }
  #contact textarea {
    height: 200px;
    resize: none; }
  #contact input[type="submit"] {
    cursor: pointer;
    background-color: #192126;
    color: #ffffff;
    max-width: 400px;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s; }
    #contact input[type="submit"]:hover {
      background-color: #1f282d; }
    #contact input[type="submit"]:focus {
      background-color: #0c0f11; }
  #contact label {
    display: block; }
  #contact #recaptcha {
    margin-bottom: 30px; }


